'use client';
import axios from "axios";
import React, { useState, useEffect } from "react";
import { useRouter } from 'next/navigation';

// Define a helper function to fetch lecturers data
const fetchLecturers = async () => {
  try {
    const response = await axios.get("http://localhost:9921/lecturers");
    console.log("Fetched lecturers:", response.data);
    return response.data;
  } catch (error) {
    console.error("Error fetching lecturers:", error);
    return [];
  }
};

const LecturersPage = () => {
  const [lecturers, setLecturers] = useState([]);
  const [filteredLecturers, setFilteredLecturers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [activeCategory, setActiveCategory] = useState("All Lecturers");
  const router = useRouter();

  useEffect(() => {
    const getLecturers = async () => {
      const data = await fetchLecturers();
      setLecturers(data);
      setFilteredLecturers(data);
      setLoading(false);
    };
    getLecturers();
  }, []);

  const handleCategoryClick = (category) => {
    setActiveCategory(category);
    if (category === "All Lecturers") {
      setFilteredLecturers(lecturers);
    } else {
      const filtered = lecturers.filter((Lecturer) => Lecturer.lecturerType === category);
      setFilteredLecturers(filtered);
    }
  };

  const handleDeleteLecturers = async (lecturerId) => {
    try {
      await axios.delete(`http://localhost:9921/lecturer/${lecturerId}`);
      const updatedLecturers = lecturerId.filter((lecturer) => lecturer.lecturerId !== lecturer);
      setLecturers(updatedLecturers);
      setFilteredLecturers(updatedLecturers);
    } catch (error) {
      console.error("Error deleting Lecturer:", error);
    }
  };

  const handleEditLecturer = (lecturerId) => {
    router.push(`/lecturer/edit/${lecturerId}`);
  };


  return (
    <div className="p-6">
      <div className="mb-6 flex justify-between items-center">
        <div>
          <h3 className="text-lg text-green-900 font-semibold">Lecturers Management</h3>
          <p className="text-sm text-gray-500">Manage and monitor all available Lecturers</p>
        </div>
        <button
          onClick={() => router.push('/lecturers/add')}
          className="px-4 py-2 bg-indigo-600 text-white rounded-md text-sm font-medium hover:bg-indigo-700">
          Add New Lecturer
        </button>
      </div>

      <div className="bg-white rounded-lg shadow-md overflow-hidden">
        <div className="p-4 border-b flex gap-4">
          {["All Lecturers", "Class code", "Units"].map((category) => (
            <button
              key={category}
              className={`px-3 py-1 rounded-md text-sm font-medium ${activeCategory === category
                  ? "bg-indigo-100 text-indigo-600"
                  : "bg-gray-100 text-gray-600 hover:bg-gray-200"
                }`}
              onClick={() => handleCategoryClick(category)}
            >
              {category}
            </button>
          ))}
        </div>

        {loading ? (
          <div className="p-4 text-center text-gray-500">Loading Lecturers...</div>
        ) : (
          <table className="min-w-full divide-y divide-gray-200">
            <thead className="bg-gray-50">
              <tr>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Lecturer ID
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                First Name
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Last Name
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                 Sessions Assigned
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Actions
                </th>
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {filteredLecturers.length > 0 ? (
                filteredLecturers.map((Lecturer) => (
                  <tr key={Lecturer.lecturerId}>
                    <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                      {Lecturer.lecturerId}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      {Lecturer.lecturerName}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      {Lecturer.lecturerClass}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      {Lecturer.lecturerUnit}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      <div className="flex gap-2">
                        <button
                          className="text-indigo-600 hover:text-indigo-900"
                          onClick={() => handleEditLecturer(Lecturer.lecturerId)}
                        >
                          Edit
                        </button>
                        <button
                          className="text-red-600 hover:text-red-900"
                          onClick={() => handleDeleteLecturers(Lecturer.lecturerId)}
                        >
                          Delete
                        </button>
                      </div>
                    </td>
                  </tr>
                ))
              ) : (
                <tr>
                  <td colSpan="5" className="px-6 py-4 text-center text-gray-500">
                    No Lecturers found.
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        )}
      </div>
    </div>
  );
};

export default LecturersPage;