const TimetablePage = () => (
    <div className="bg-white rounded-lg shadow-md overflow-hidden">
      <div className="p-4 border-b flex justify-between items-center">
        <h3 className="text-lg font-semibold">Weekly Schedule</h3>
        <div className="flex gap-2">
          <button className="px-3 py-1 bg-indigo-100 text-indigo-600 rounded-md text-sm font-medium">Week View</button>
          <button className="px-3 py-1 bg-purple-100 text-gray-600 rounded-md text-sm font-medium">Day View</button>
        </div>
      </div>
  
      <div className="p-4">
        <div className="grid grid-cols-8 gap-2">
          <div className="col-span-1"></div>
          {['Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday', 'Sunday'].map(day => (
            <div key={day} className="font-medium text-center text-black py-2">{day}</div>
          ))}
        </div>
  
        {['8:00 AM', '9:00 AM', '10:00 AM', '11:00 AM', '12:00 PM'].map(time => (
          <div key={time} className="grid grid-cols-8 gap-2 border-t">
            <div className="col-span-1 text-sm text-blue-500 pt-2">{time}</div>
            {[1, 2, 3, 4, 5, 6, 7].map(day => (
              <div key={day} className="h-16 border border-gray-100 rounded-md p-1">
                {day === 2 && time === '10:00 AM' && (
                  <div className="h-full bg-blue-100 border-l-4 border-blue-500 rounded-sm p-1 text-xs overflow-hidden">
                    <div className="font-medium text-blue-500">Computer Science</div>
                    <div className="text-gray-900">Dr. Smith • Room 101</div>
                  </div>
                )}
                {day === 4 && time === '9:00 AM' && (
                  <div className="h-full bg-green-100 border-l-4 border-green-500 rounded-sm p-1 text-xs overflow-hidden">
                    <div className="font-medium text-green-900">Mathematics</div>
                    <div className="text-gray-900">Dr. Jones • Room 305</div>
                  </div>
                )}
              </div>
            ))}
          </div>
        ))}
      </div>
    </div>
  );
export default TimetablePage;  