// Page Components
import StatCard from "../StatCard/statcard";
const DashboardPage = () => (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
      <StatCard title="Total Classes" value="124" color="bg-blue-500" />
      <StatCard title="Active Lecturers" value="28" color="bg-green-500" />
      <StatCard title="Available Rooms" value="15" color="bg-yellow-500" />
      <StatCard title="Weekly Sessions" value="320" color="bg-purple-500" />
      
      <div className="md:col-span-2 lg:col-span-2 bg-white rounded-lg shadow-md p-6">
        <h3 className="text-lg font-semibold mb-4">Upcoming Sessions</h3>
        <div className="space-y-3">
          {[1, 2, 3].map((i) => (
            <div key={i} className="flex justify-between items-center p-3 bg-gray-50 rounded-md">
              <div>
                <p className="font-medium">Introduction to Programming</p>
                <p className="text-sm text-gray-500">Dr. James Wilson • Room 203</p>
              </div>
              <div className="text-right">
                <p className="text-sm font-medium">Today</p>
                <p className="text-sm text-gray-500">10:00 - 11:30 AM</p>
              </div>
            </div>
          ))}
        </div>
      </div>
      
      <div className="md:col-span-2 lg:col-span-2 bg-white rounded-lg shadow-md p-6">
        <h3 className="text-lg font-semibold text-gray-600 mb-4">Room Utilization</h3>
        <div className="h-64 flex items-center justify-center bg-gray-100 rounded-md">
          <p className="text-gray-500">Room usage chart would go here</p>
        </div>
      </div>
    </div>
  );
  export default DashboardPage;