'use client';
import axios from "axios";
import React, { useState, useEffect } from "react";
import { useRouter } from 'next/navigation';

// Define a helper function to fetch class_detail data
const fetchClass_details = async () => {
  try {
    const response = await axios.get("http://localhost:9921/class_details");
    console.log("Fetched class_details:", response.data);
    return response.data;
  } catch (error) {
    console.error("Error fetching class_details:", error);
    return [];
  }
};

const Class_detailsPage = () => {
  const [class_details, setClass_details] = useState([]);
  const [filteredClass_details, setFilteredClass_details] = useState([]);
  const [loading, setLoading] = useState(true);
  const [activeCategory, setActiveCategory] = useState("All Class_details");
  const router = useRouter();

  useEffect(() => {
    const getClass_details = async () => {
      const data = await fetchClass_details();
      setClass_details(data);
      setFilteredClass_details(data);
      setLoading(false);
    };
    getClass_details();
  }, []);

  const handleCategoryClick = (category) => {
    setActiveCategory(category);
    if (category === "All Class_details") {
      setFilteredClass_details(class_details);
    } else {
      const filtered = class_details.filter((class_detail) => class_detail.class_detailType === category);
      setFilteredClass_details(filtered);
    }
  };

  const handleDeleteClass_detail = async (class_detail_Id) => {
    try {
      await axios.delete(`http://localhost:9921/class_details/${class_detail_Id}`);
      const updatedClass_details = class_details.filter((class_detail) => class_detail.class_detail_Id !== class_detail_Id);
      setClass_details(updatedClass_details);
      setFilteredClass_details(updatedClass_details);
    } catch (error) {
      console.error("Error deleting class_detail:", error);
    }
  };

  const handleEditClass_detail = (class_detail_Id) => {
    router.push(`/class_details/edit/${class_detail_Id}`);
  };


  return (
    <div className="p-6">
      <div className="mb-6 flex justify-between items-center">
        <div>
          <h3 className="text-lg text-green-900 font-semibold">Class_details Management</h3>
          <p className="text-sm text-gray-500">Manage and monitor all available class_details</p>
        </div>
        <button
          onClick={() => router.push('/class_details/add')}
          className="px-4 py-2 bg-indigo-600 text-white rounded-md text-sm font-medium hover:bg-indigo-700">
          Add New Class_detail
        </button>
      </div>

      <div className="bg-white rounded-lg shadow-md overflow-hidden">
        <div className="p-4 border-b flex gap-4">
          {["All Class_details", "Lecture Halls", "Labs", "Meeting Class_details"].map((category) => (
            <button
              key={category}
              className={`px-3 py-1 rounded-md text-sm font-medium ${activeCategory === category
                  ? "bg-indigo-100 text-indigo-600"
                  : "bg-gray-100 text-gray-600 hover:bg-gray-200"
                }`}
              onClick={() => handleCategoryClick(category)}
            >
              {category}
            </button>
          ))}
        </div>

        {loading ? (
          <div className="p-4 text-center text-gray-500">Loading class_details...</div>
        ) : (
          <table className="min-w-full divide-y divide-gray-200">
            <thead className="bg-gray-50">
              <tr>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Class ID
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Class Code
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Class Size
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Year
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                 Semister
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Actions
                </th>
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {filteredClass_details.length > 0 ? (
                filteredClass_details.map((class_detail) => (
                  <tr key={class_detail.class_detail_Id}>
                    <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                      {class_detail.class_detail_Id}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      {class_detail.class_detailName}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      {class_detail.class_detailType}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      {class_detail.class_detailCapacity}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      {class_detail.class_detailSemister}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      <div className="flex gap-2">
                        <button
                          className="text-indigo-600 hover:text-indigo-900"
                          onClick={() => handleEditClass_detail(class_detail.class_detail_Id)}
                        >
                          Edit
                        </button>
                        <button
                          className="text-red-600 hover:text-red-900"
                          onClick={() => handleDeleteClass_detail(class_detail.class_detail_Id)}
                        >
                          Delete
                        </button>
                      </div>
                    </td>
                  </tr>
                ))
              ) : (
                <tr>
                  <td colSpan="5" className="px-6 py-4 text-center text-gray-500">
                    No class_details found.
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        )}
      </div>
    </div>
  );
};

export default Class_detailsPage;