'use client'
import React, { useState } from 'react';
import { Calendar, Clock, Users, Home, BookOpen, DoorOpen, Menu, X } from 'lucide-react';
  // Import all page components
import DashboardPage from '@/Components/DashboardPage/dashboard';
import TimetablePage from '@/Components/TimetablePage/timetable';
import RoomsPage from '@/Components/RoomsPage/rooms';
import LecturersPage from '@/Components/LecturersPage/lecturers';
import ClassesPage from '@/Components/ClassesPage/classes';
import DepartmentPage from '@/Components/DepartmentPage/departments';
import CoursePage from '@/Components/CoursePage/courses';
import FacultyPage from '@/Components/FacultyPage/faculty';
import ProgrammePage from '@/Components/ProgrammePage/program';

const TimetableManagementDashboard = () => {
  const [activePage, setActivePage] = useState('dashboard');
  const [sidebarOpen, setSidebarOpen] = useState(true);



  const toggleSidebar = () => {
    setSidebarOpen(!sidebarOpen);
  };

  // Page content components
  const renderPageContent = () => {
    switch (activePage) {
      case 'dashboard':
        return <DashboardPage />;
      case 'timetable':
        return <TimetablePage />;
        case 'faculty':
        return <FacultyPage />;
      case 'departments':
        return <DepartmentPage/>;
        case 'programme':
        return <ProgrammePage/>;     
      case 'lecturers':
        return <LecturersPage />;
      case 'courses':
        return <CoursePage />;
      case 'classes':
        return <ClassesPage />;
      case 'rooms':
        return <RoomsPage />;
      default:
        return <DashboardPage />;
    }
  };

  return (
    <div className="flex h-screen bg-gray-100">
      {/* Sliding Sidebar */}
      <div className={`fixed lg:relative z-10 bg-indigo-800 text-white h-full transition-all duration-300 ease-in-out ${sidebarOpen ? 'w-64' : 'w-20'}`}>
        <div className="flex items-center justify-between p-4 border-b border-indigo-700">
          <div className={`flex items-center space-x-3 ${sidebarOpen ? 'block' : 'hidden'}`}>
            <Clock className="h-6 w-6" />
            <h1 className="font-bold text-xl">TimeTable</h1>
          </div>
          <button onClick={toggleSidebar} className="p-1 rounded-md hover:bg-indigo-700">
            {sidebarOpen ? <X className="h-6 w-6" /> : <Menu className="h-6 w-6" />}
          </button>
        </div>

        <nav className="mt-6">
          <NavItem 
            icon={<Home />} 
            title="Dashboard" 
            isActive={activePage === 'dashboard'} 
            onClick={() => setActivePage('dashboard')} 
            sidebarOpen={sidebarOpen}
          />
          <NavItem 
            icon={<Calendar />} 
            title="Timetable" 
            isActive={activePage === 'timetable'} 
            onClick={() => setActivePage('timetable')} 
            sidebarOpen={sidebarOpen}
          />
          <NavItem 
            icon={<Calendar />} 
            title="Faculty"
            isActive={activePage === 'faculty'} 
            onClick={() => setActivePage('faculty')}
            sidebarOpen={sidebarOpen}
          />
          <NavItem 
            icon={<Calendar />} 
            title="Departments" 
            isActive={activePage === 'departments'} 
            onClick={() => setActivePage('departments')} 
            sidebarOpen={sidebarOpen}
          />
          <NavItem 
            icon={<Calendar />} 
            title="Programme"
            isActive={activePage === 'programme'} 
            onClick={() => setActivePage('programme')}
            sidebarOpen={sidebarOpen}
          />
          
          <NavItem 
            icon={<Users />} 
            title="Lecturers" 
            isActive={activePage === 'lecturers'} 
            onClick={() => setActivePage('lecturers')} 
            sidebarOpen={sidebarOpen}
          />
          <NavItem 
            icon={<DoorOpen />} 
            title="Course" 
            isActive={activePage === 'courses'} 
            onClick={() => setActivePage('courses')} 
            sidebarOpen={sidebarOpen}
          />
          <NavItem 
            icon={<BookOpen />} 
            title="Classes" 
            isActive={activePage === 'classes'} 
            onClick={() => setActivePage('classes')} 
            sidebarOpen={sidebarOpen}
          />
          <NavItem 
            icon={<DoorOpen />} 
            title="Rooms" 
            isActive={activePage === 'rooms'} 
            onClick={() => setActivePage('rooms')} 
            sidebarOpen={sidebarOpen}
          />
        </nav>
      </div>

      {/* Main Content */}
      <div className="flex-1 overflow-x-hidden overflow-y-auto">
        <header className="bg-white shadow-sm">
          <div className="px-4 py-4 flex items-center justify-between">
            <h2 className="font-semibold text-xl text-gray-800">
              {activePage.charAt(0).toUpperCase() + activePage.slice(1)}
            </h2>
            <div className="flex items-center space-x-4">
              <div className="relative">
                <span className="absolute inset-y-0 left-0 flex items-center pl-2">
                  <svg className="h-5 w-5 text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
                  </svg>
                </span>
                <input
                  type="text"
                  placeholder="Search..."
                  className="py-2 pl-10 pr-4 rounded-md border border-gray-300 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
                />
              </div>
              <div className="flex items-center gap-2">
                <div className="w-8 h-8 rounded-full bg-indigo-600 flex items-center justify-center text-white font-semibold">
                  Me
                </div>
                <span className="text-sm text-gray-900 font-medium">James</span>
              </div>
            </div>
          </div>
        </header>

        <main className="p-6">
          {renderPageContent()}
        </main>
      </div>
    </div>
  );
};

// Navigation Item Component
const NavItem = ({ icon, title, isActive, onClick, sidebarOpen }) => {
  return (
    <a
      href="#"
      onClick={(e) => {
        e.preventDefault();
        onClick();
      }}
      className={`flex items-center px-4 py-3 ${
        isActive ? 'bg-indigo-700' : 'hover:bg-indigo-700'
      } transition-colors duration-200`}
    >
      <div className="mr-3">{icon}</div>
      {sidebarOpen && <span className="text-sm font-medium">{title}</span>}
    </a>
  );
};


// Stats Card Component
const StatCard = ({ title, value, color }) => (
  <div className="bg-white rounded-lg shadow-md p-6">
    <div className={`w-12 h-12 ${color} rounded-lg flex items-center justify-center mb-4`}>
      <svg className="h-6 w-6 text-white" fill="none" viewBox="0 0 24 24" stroke="currentColor">
        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M9 19v-6a2 2 0 00-2-2H5a2 2 0 00-2 2v6a2 2 0 002 2h2a2 2 0 002-2zm0 0V9a2 2 0 012-2h2a2 2 0 012 2v10m-6 0a2 2 0 002 2h2a2 2 0 002-2m0 0V5a2 2 0 012-2h2a2 2 0 012 2v14a2 2 0 01-2 2h-2a2 2 0 01-2-2z" />
      </svg>
    </div>
    <h4 className="text-lg font-semibold text-gray-800">{title}</h4>
    <p className="text-3xl font-bold mt-2">{value}</p>
  </div>
);

export default TimetableManagementDashboard;